/** @format */

@import 'values';
.root {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.hand {
  width: 50%;
  img {
    width: 100%;
  }
}
.ask {
  width: 50%;
  p {
    @extend %DHeadline;
    color: $Black-100;
    margin: 0 0 64px 0;
    max-width: 640px;
  }
}
.start {
  @extend %DBD;
}
.triangle {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100vh;
  width: 100vw;
  z-index: -1;
}
.lang {
  @extend %LANG;
}
@media screen and (max-width: 960px) {
  .ask {
    p {
      font-size: 32px;
      margin-bottom: 30px;
    }
    margin-left: 60px;
  }
  .start {
    font-weight: 600;
    font-size: 14px;
    border-radius: 8px;
    height: 50px;
    width: 300px;
  }
}
@media screen and (max-width: 720px) {
  .root {
    flex-direction: column;
  }
  .ask {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-left: 0;
    width: 100%;
    height: 40%;
    text-align: center;
  }
  .hand {
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    img {
      max-height: 200px;
    }
  }
  .lang {
    width: 50px;
    height: 30px;
  }
}
