/** @format */

@import 'values';
.root {
  display: flex;
  align-items: center;
  height: 100vh;
  background-color: $Black-5;
}
.item {
  width: 50%;
  color: $Black-100;
}
.item:first-child {
  display: flex;
  justify-content: center;
}
.total {
  @extend %DQuestion;
  opacity: 0.5;
}
.earned {
  @extend %DHeadline;
  margin-bottom: 64px;
}
.end {
  @extend %DBD;
  a {
    text-decoration: none;
    color: #f5f5f7;
  }
}
.lang {
  @extend %LANG;
}

@media screen and (max-width: 960px) {
  .earned {
    font-size: 32px;
    margin-bottom: 10px;
  }
  .total {
    font-size: 20px;
  }
}
@media screen and (max-width: 720px) {
  .root {
    flex-direction: column;
  }
  .item {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-left: 0;
    width: 100%;
    text-align: center;
  }
  .end {
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translate(-50%, 0);
  }
  .total {
    margin-bottom: 20px;
  }
  .item:first-child {
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    img {
      max-height: 200px;
    }
  }
  .lang {
    width: 50px;
    height: 30px;
  }
}
